import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SurveyService } from '../../service/survey.service';

import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

import canvg from 'canvg'
import { Chart } from 'angular-highcharts';
import { ImgToBase64Service } from '../../service/img-to-base64/img-to-base64.service';
import { ExportPdfService } from '../../service/export-pdf/export-pdf.service';

@Component({
  selector: 'app-colmatch',
  templateUrl: './colmatch.component.html',
  styleUrls: ['./colmatch.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ColmatchComponent implements OnInit {
  baseLink = '/survey/common/';
  links = [
    { link: '/summary', label: 'Summary' },
    { link: '/progress', label: 'Survey Progress' }
  ];
  id = 0;
  info: any = {
    name: 'Survey',
    description: 'A survey',
    status: false,
    id: 0
  };

  details: any = [];
  analysis: any = [];

  //配置pdf信息
  configInfo: any;

  constructor(private router: Router,
    private route: ActivatedRoute,
    private ss: SurveyService,
    private cd: ChangeDetectorRef,
    private itb: ImgToBase64Service,
    public ep: ExportPdfService) { }

  ngOnInit() {
    // this.configPDF();
    this.getSurveyDetail();
    this.getColMatchAnalysis();
  }

  private getSurveyDetail() {
    this.ss.getSurveyDetail(12).then(res => {
      this.details = res.sections;
      this.info = res;
      this.cd.markForCheck();
    });
  }

  private getColMatchAnalysis() {
    this.ss.getColMatchAnalysis().then(res => {
      this.analysis = res;
      this.cd.markForCheck();
    });
  }

  startSurvey() {
    this.router.navigateByUrl('/survey/question/' + this.info.first_section);
  }

  configPDF() {
    let imgList = [
      '/assets/img/report/student/College-Match.jpg',
      '/assets/img/pdfCover.png'
    ];
    // let imgList =[
    //   '/assets/img/pdfCover.png',
    //   '/assets/img/college-match.png'
    // ]
    this.itb.getImgToBaseList(imgList).then(result => {
      // this.ep.initConfig("College Match",
      //   "John Doe",
      //   "November 2018",
      //   result[0],
      //   "Blueprint for\nChoosing the Best\nSchool for You",
      //   result[0]);
      this.ep.initConfig("COLLEGE MATCH",'REPORT','Andy Awesome',result[1],'05/15/2019',result[0])

      this.ep.addContent("\n\n");
      this.ep.addContent([
        "\n\n",
        {
          text: "Overview",
          color: "#3c78d8",
          fontSize: 14
        },
        "\n\n",
        {
          text: `This survey is from College Match: A Blueprint for Choosing the School for You, Dr.
          Steven R. Antonoff's how-to guide for students and parents. In addition to this survey,
          there are a dozen more worksheets included in the book. The purpose of College
          Match is to help students find colleges where they will be happy and successful.
          \n
          Dr. Steven R. Antonoff is an independent educational consultant, trainer, and speaker.
          He is former Dean of Admissions and Financial Aid and Dean of Students at the
          University of Denver. Dr. Antonoff has worked with hundreds of families in their search
          for good fit colleges. His articles have appeared in both professional and popular
          publications. His presentations are for students, parents, and counselors.`,
          fontSize: 12
        },
        "\n\n",
        {
          text: "Interpreting Your Self-Survey Scores",
          color: "#3c78d8",
          fontSize: 14
        },
        "\n\n",
        {
          text: `The survey you just completed is intended to encourage you to think about yourself as
          a college student. By looking at your scores and seeing what they mean within each
          category, you should glean insight about yourself—your attitudes, strengths and
          weaknesses, aspirations, and fears. This information will be essential to your college
          planning process.
          \n
          There is no definitive answer to what a specific score in a category means for you. In
          the sections below, you will see words describing each category like “scores in the mid
          30’s or higher suggest . . . ” and “lower scores suggest . . . ”. This lack of specificity is
          purposeful because these scores can be evaluated in many ways. It is up to you to
          read the descriptions and to determine what, if any, meaning a particular score has for
          you. Your score in one category might give you insight into something important, and
          your score in another category might be less meaningful.
          \n
          Finding meaning in high scores is a bit easier than doing so for lower scores, but
          remember, there is no hard-and-fast interpretation of a lower score. In the category
          School Enthusiasm, for example, the first paragraph interprets scores “in the mid 30’s
          or above.” The next paragraph is an analysis of the meaning of “lower scores,” i.e., a
          score from 0 to the mid 30’s. Keep in mind that your score—whether higher or lower—
          is on a continuum. Thus, your interpretation of what your score means in this area will
          vary if your score was a single digit or in the teens versus the high 20’s or low 30’s. In
          other words, the description associated with a low score might be more true of you if
          your score was a 6 as compared with a 29.
          \n
          Finally, don’t get hung up on the numerical aspects of the interpretation of your scores.
          The goal here is to provide information that can serve as a springboard in your quest
          to find colleges that are a good match for you. “High” and “low” numbers are less
          significant than using the information here to come up with match colleges that
          correspond with your attributes and beliefs.`,
          fontSize: 12
        },
        "\n\n",
        {
          text: "John Doe, here are YOUR results from the College Match Survey:",
          color: "#3c78d8",
          fontSize: 14
        },
        "\n\n",
        {
          text: `Affection for Knowledge: 57`,
          fontSize: 10,
          bold: true
        },
        "\n",
        {
          text: `- If you scored in the 40’s or higher here, the life of the mind is exciting to you. You may read widely on
          a variety of topics, and you enjoy learning for the sake of learning, not because you may get a good
          grade. You enjoy talking about ideas and philosophies and trading perspectives with others. Students
          with high scores here should seek academic challenges and colleges that will stimulate their minds.
          Keep in mind, however,that “big name” colleges are not the only ones that provide intellectual
          stimulation and that there are dozens of colleges outside the Ivy League Athletic Conference that are
          intellectually challenging. For some students, nontraditional colleges that do not stress grades may be
          worth considering because they can offer freedom to follow your curiosity wherever it takes you, without
          worrying about a GPA.
          \n
          - Lower scores suggest that you are less comfortable with intellectual ideas and concepts. Perhaps you
          haven’t yet have been exposed to compelling topics, issues, or ideas that excite you. You may not have
          had experiences that gave you a love of learning. You will want to be cautious about applying to a
          college that expects you to start your first semester with a serious academic focus. You may want to use
          college as aplace to try classes or subjects with which you are unfamiliar; you might be pleasantly
          surprised by how fascinating a new subject can be. On the other hand, if you have a few specific areas
          that interest you, you might prefer a college where the classes are more directly related or applicable to
          these areas.`,
          fontSize: 10
        },
        "\n",
        {
          text: `Basic Academic Skills: 57`,
          fontSize: 10,
          bold: true
        },
        "\n",
        {
          text: `If you scored in the 40’s or higher, you are likely to have the skills particularly valuable to success in
          college—writing, reading, note-taking, and prioritizing. You may find college a bit less demanding than
          you expected, thanks to the strength of your academic skills. 
          \n
          There are several considerations for the student with a lower score in this category. You may want to
          search out colleges where some extra assistance from teachers is readily available and where you can
          find resources and opportunities to develop your study skills. When choosing classes, think carefully
          about the workload involved — reading requirements, term papers, and other assignments — especially
          during freshman year. However, lower scores in this category also may reflect a student who is overly
          critical of personal study skills but fully capable of handling the academic demands of typical freshmen
          courses.`,
          fontSize: 10,
        },
        "\n",
        {
          text: `Eagerness for College: 57`,
          fontSize: 10,
          bold: true
        },
        "\n",
        {
          text: `Scores in the high 30’s or above signal that you anticipate college in a favorable way and are looking
          forward to the collegiate experience. Adjustment will likely be easy because your enthusiasm will be a
          great asset in learning to master college life. Although you may have some concerns about college,
          your attitude is generally positive. Because you played a primary role in deciding to attend college, you
          likely have specific goals to make the most of your college years. 
          \n
          Lower scores can be interpreted in several ways. Just because you scored low here doesn’t mean that
          you are not “college material” or are dreading the college experience. Eagerness for college tends to
          ebb and flow during the high school years. But a lower score is worth thinking about. Are you motivated
          to attend college? You will want to give special consideration to the ways you can make college a
          satisfying and productive experience. Some fears about college - such as leaving home and being
          independent - are perfectly normal. If your score was lower in this category, involving yourself in the
          planning and decision making processes will help you feel more in control and less like you are being
          pushed into college. But think carefully about, and seek assistance with, the timing and the nature of
          your college years. `,
          fontSize: 10,
        },
        "\n",
        {
          text: `School Enthusiasm: 54`,
          fontSize: 10,
          bold: true,
        },
        "\n",
        {
          text: `If you scored in the mid 30’s or higher, you probably feel comfortable with the tasks and central qualities
          of school. In general, you like attending classes and have positive feelings about the academicnature of
          school.
          \n
          If your score is lower here, there are several possible interpretations. You may enjoy some of the social
          features of school more than the actual classes, teachers, and classroom information. You many not
          have found school to be a successful academic experience, and your struggle with school may
          affectyour attitude toward it. Your school attitude may impact your feelings about planning for college,
          your willingness to enter a challenging college environment, the level of competitiveness you prefer, as
          well as your motivation to stay in school. Do you have the motivation to be successful in college? You
          may not have enjoyed high school because particular characteristics of your school may not have been
          right for you. If that is the case, you have a chance to choose the college that provides the kind of
          environment that suits you. Your analysis of the factors that matter to you in a college (Chapter 3) will
          be particularly importantin finding a college you can be excited about attending. You also may want
          consider these questions: Is the time right to enter college? Would you benefit from a year of travel,
          work, or some other activity before entering college?`,
          fontSize: 10,
        },
        "\n",
        {
          text: `Self Understanding: 54`,
          fontSize: 10,
          bold: true,
        },
        "\n",
        {
          text: `A score in the high 30’s and above suggests that you are in touch with your own good and bad qualities
          and are accepting of yourself. You are fairly comfortable with who you are and don’t let others direct
          your thoughts and behaviors. This self-awareness will aid you in adjusting to college and in making
          decisions once you enroll. You will be less prone to modulating your behavior to impress others. You
          are comfortable with your abilities and personality and such comfort will enable you to make mature
          decisions in college.
          \n
          If your score was lower here, you may be just beginning to know yourself. Teenagers’ perceptions of
          themselves are usually heavily influenced by peers. Is it possible that you are overly responsive to the
          wishes and demands others have for you? Your focus on pleasing others may override your personal
          wants and needs. You might find it easier to acquire self-understanding and confidence at a smaller,
          more-supportive college than at an enormous university. Look at schools where you’ll be a big fish in a
          small pond. Lower scores are not necessarily bad! Self-understanding is a skill that is a lifetime in the
          making.`,
          fontSize: 10,
        },
        "\n",
        {
          text: `Career Orientation: 48`,
          fontSize: 10,
          bold: true,
        },
        "\n",
        {
          text: `A score of 40 or higher in this category indicates that you view college as a means to an end, a way to
          achieve other lifetime goals and attain a position of professional or vocational competence. Because
          you see college as a vehicle for vocational preparation, you may want to explore professionally related
          majors and seek colleges where you’ll able to keep “on track” toward meeting the demands of your
          chosen career. As you consider colleges, review the general educational requirements that students
          must fulfill. Too many required humanities courses, for example, may be less appealing to you than the
          freedom to concentrate on subjects of interest early on.
          \n
          A lower score in this category is quite common and reflects a student who wants a general, broadbased college education. You are likely very open to the wide variety of learning experiences that
          college may bring. For you, college may be a time for academic experimentation, a place where you can
          test a variety ofideas and career paths. You may want to explore traditional liberal art colleges with
          many options in the humanities, social sciences, and sciences. Whether it’s liberal arts or something
          else, you are on track to discover your likes and dislikes in college.`,
          fontSize: 10,
        },
        "\n",
        {
          text: `Academic/Social Balance: 45`,
          fontSize: 10,
          bold: true,
        },
        "\n",
        {
          text: `If you scored in the high 30’s and above, both academics and extracurricular experiences are
          prioritiesfor you. You will want to choose a college where you can have a balanced life, where you can
          pursue your studies, participate in sports or other activities, and still have some time for yourself.
          Colleges known for a “work hard/play hard” philosophy may appeal to you, but consider your college
          choices carefully— being certain you are not getting in “over your head.” You will want to choose a
          college where you are academically similar to the majority of students, where you are as likely as
          anyone else to understand the material in your classes, where you are able to spend about the same
          amount of time studying and still have a life outside of the classroom. In researching colleges, look
          carefully at the characteristics of students who enroll. What were their grade point averages? Test
          scores? Did they take courses in high school fairly similar to the ones you have chosen?
          \n
          There are several ways to interpret a lower score in this category. Perhaps you are focused more on
          academics and want college to be all about learning. Or maybe you value your social life more than
          your studies and want college to be all about having fun. If academics are your highest or even your
          only priority, you may be comfortable at a college that is academically intense. If social experiences
          matter most, you will want to consider colleges where you will have time to get involved in campus
          activities and time for your friends.`,
          fontSize: 10,
        },
        "\n",
        {
          text: `Participant Learner: 42`,
          fontSize: 10,
          bold: true,
        },
        "\n",
        {
          text: `• If your score in this category is in the mid 30’s or higher, you likely want to take an active, rather than
          passive, approach to learning. You are not comfortable merely taking notes and regurgitating the
          teacher’s lectures—you want to get involved! You normally do the homework your teachers assign, not
          because you have to, but because it helps you learn. Likely, you participate in class discussion, enjoy it,
          and learn from your peers. You read the textbooks and may read additional material on a topic that
          interests you. Because learning and understanding are so important to you, you are assertive in asking
          questions of teachers and fellow students. You likely will be most comfortable at colleges where
          professors are readily available and lecture classes are not huge. In addition, you will want opportunities
          for discussions and seminars as part of your college experience. High scorers should consider smaller
          colleges.
          \n
          • If you scored in the lower 30’s and below, you have several factors to consider. You may be interested
          in and committed to learning but prefer to learn quietly and deliberately rather than participate verbally in
          class.Course lectures, reading, and out-of-class assignments are normally sufficient for you to learn the
          material.For you, class size probably will not be as significant a factor. Lower scores here suggest that
          you may notmind being in a large class, and a bigger university would meet your needs.`,
          fontSize: 10,
        },
        "\n",
        {
          text: `Independence: 39`,
          fontSize: 10,
          bold: true,
        },
        "\n",
        {
          text: `The questions in this category focused on your willingness to make your own plans, follow your
          instincts, and act independently of others. Are you ready to make the decisions college students face
          every day—choosing classes, setting your own schedule, negotiating with roommates, determining your
          own social “do’s and don’ts”? Students who score in the 40’s or higher will likely be comfortable with
          such freedom. Having already shown that you take charge of your life, you are less apt to act
          irresponsibly while away from home and less in need of others’ approval before making important
          decisions. In addition, given your self-sufficiency, you may not need the “excitement” of a college in a
          large city because even at an isolated college locale you will be able to generate activities and create a
          social network.
          \n
          If your scores were lower, you may still be transitioning from dependence to independence. Rather than
          be overwhelmed by choices your freshman year, you may want some decisions made for you. Some
          colleges provide more structure, such as a core curriculum or series of required classes, and offer a
          range of planned activities and events to attend. You may also need to work on self-discipline and
          practice assuming responsibility for your decisions and their consequences. A smaller college could
          provide the perfect environment for gradually building your self-confidence and independence`,
          fontSize: 10,
        },
        "\n",
        {
          text: `Social Consciousness: 39`,
          fontSize: 10,
          bold: true,
        },
        "\n",
        {
          text: `Scores in the high 30’s and above suggest you care about the world and may not be satisfied with the
          status quo. Your concern about the state of the world may influence your life and lead you to seek
          outlets for your compassion and empathy. Colleges that might appeal to you include those with political
          action committees, volunteer opportunities, or other activities geared to reaching out beyond the bounds
          of campus. Some colleges place an explicit value on recognizing one’s responsibility to the world.
          \n
          Lower scores here could have several connotations. You may not be sensitive to or aware of the
          numerous opportunities for social responsibility. You may be comfortable pursuing your individual goals,
          or you may have other priorities. A low score doesn’t mean you don’t have a social conscience! It may
          merely reflect that, at present, the drive to help others is less strong than other motivators in your life.`,
          fontSize: 10,
        },
        "\n",
      ])
      this.ep.openPdf()
    });
    
  }



}
