import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';

import { SurveyComponent } from './survey.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { EfcComponent } from './efc/efc.component';
import { EfcBaseComponent } from './efc/efc-base/efc-base.component';
import { EfcCategoryComponent } from './efc/efc-category/efc-category.component';
import { EfcStrategyComponent } from './efc/efc-strategy/efc-strategy.component';
import { EfcSampleComponent } from './efc/efc-sample/efc-sample.component';
import { LearningstyleComponent } from './learningstyle/learningstyle.component';
import { CommonComponent } from './common/common.component';
import { CommonSummaryComponent } from './common/summary/common-summary.component';
import { CommonProgressComponent } from './common/progress/common-progress.component';
import { FmsComponent } from './fms/fms.component';
import { FmsStyleComponent } from './fms/fms-style/fms-style.component';
import { FmsTraitComponent } from './fms/fms-trait/fms-trait.component';
import { FmsMajorComponent } from './fms/fms-major/fms-major.component';
import { ColmatchComponent } from './colmatch/colmatch.component';
import { ColmatchAnalysisComponent } from './colmatch/colmatch-analysis/colmatch-analysis.component';
import { DescriptionComponent } from './description/description.component';
import { QuestionComponent } from './question/question.component';
import { LearningstyleAnalysisComponent } from './learningstyle/learningstyle-analysis/learningstyle-analysis.component';
import { LearningstyleLearnComponent } from './learningstyle/learningstyle-learn/learningstyle-learn.component';
import { LearningstyleStudyComponent } from './learningstyle/learningstyle-study/learningstyle-study.component';
import { CommonTitleComponent } from './common/common-title/common-title.component';
import { EfcQuestionItemComponent } from './efc/efc-question-item/efc-question-item.component';

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: SurveyComponent,
        children: [
          { path: '', component: DashboardComponent },
          { path: 'dashboard', component: DashboardComponent },
          { path: 'efc', component: EfcComponent },
          { path: 'fms', component: FmsComponent },
          { path: 'learningstyle', component: LearningstyleComponent },
          { path: 'question/:id', component: QuestionComponent },
          { path: 'efc/sample', component: EfcSampleComponent },
          {
            path: 'common/:id', component: CommonComponent
          }, {
            path: 'colmatch', component: ColmatchComponent
          },
        ]
      }
    ]
    )
  ],
  declarations: [
    SurveyComponent,
    DashboardComponent,

    EfcComponent,
    EfcBaseComponent,
    EfcCategoryComponent,
    EfcStrategyComponent,
    EfcSampleComponent,

    LearningstyleComponent,
    LearningstyleAnalysisComponent,
    LearningstyleLearnComponent,
    LearningstyleStudyComponent,

    DescriptionComponent,
    CommonComponent,
    CommonSummaryComponent,
    CommonProgressComponent,
    CommonTitleComponent,

    FmsComponent,
    FmsStyleComponent,
    FmsTraitComponent,
    FmsMajorComponent,

    ColmatchComponent,
    ColmatchAnalysisComponent,


    QuestionComponent,


    EfcQuestionItemComponent,




  ]
})
export class SurveyModule { }
