import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef
} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SurveyService } from '../../service/survey.service';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
import { ImgToBase64Service } from '../../service/img-to-base64/img-to-base64.service';
import { ExportPdfService } from '../../service/export-pdf/export-pdf.service';

@Component({
  selector: 'app-fms',
  templateUrl: './fms.component.html',
  styleUrls: ['./fms.component.css']
})
export class FmsComponent implements OnInit {
  baseLink = '/survey/common/';
  links = [
    { link: '/summary', label: 'Summary' },
    { link: '/progress', label: 'Survey Progress' }
  ];
  id = 0;
  info: any = {
    name: 'Survey',
    description: 'A survey',
    status: false,
    id: 0
  };

  details: any = [];
  analysis: any = [];
  choices = [
    {ID: 1, ChoiceText: 'a'}, {ID: 2, ChoiceText: 'b'},{ID: 3, ChoiceText: 'c'}
  ];
  data = '[1,2,3]';
  docDefinition: object = {};
  imgBaseSrc;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private ss: SurveyService,
    private cd: ChangeDetectorRef,
    private itb: ImgToBase64Service,
    public ep: ExportPdfService
  ) {}
  ngOnInit() {
    const self = this;
    /*this.getSurveyDetail();
    this.getFmsAnalysis();*/
    console.log('进入了ngOnInit')
    Promise.all([self.getSurveyDetail(), self.getFmsAnalysis()]).then(function () {
      // console.log(self.info, self.details, self.analysis, 'self.info, self.details, self.analysis');
      self.initPdf();
    });
  }
  initPdf() {
    const self = this;
    let imgList = [
      '/assets/img/report/student/EFC-Calculator.jpg',
      '/assets/img/pdfCover.png'
    ];
    self.itb.getImgToBaseList(imgList).then(result => {
      const personalTrait = [];
      const collegeStyle = [];
      const valueApproachArr = [];
      const studentQualityArr = [];
      const majors = [];
      const careers = [];
      let keyWords = '';
      if (self.analysis) {
        self.getArr(self.analysis.sections, personalTrait);
        self.getArr(self.analysis.sections.slice(1, 3), collegeStyle);
        self.analysis.college_career.CollegeStyles.info.keywords.forEach((item) => {
          keyWords += item.name;
        });
        self.analysis.college_career.CollegeStyles.info.values.forEach((item) => {
          valueApproachArr.push(item.name);
        });
        self.analysis.college_career.CollegeStyles.info.qualities.forEach((item) => {
          studentQualityArr.push(item.name);
        });
        self.analysis.college_career.Careers.major.forEach((item) => {
          majors.push(item.name);
        });
        self.analysis.college_career.Careers.career.forEach((item) => {
          careers.push(item.name);
        });

      }
      self.ep.initConfig("A Find My Spark Report for",'REPORT','John Doe',result[1],'November 2018',result[0]);
      self.ep.addContent([
        // Instroduction
        {
          text: 'Introduction',
          style: 'subheader'
        },
        {
          text: self.info.LongDescription,
          style: 'content'
        },
        // Personality Trait
        {
          text: 'Personality Trait',
          style: 'header',
          alignment: 'center'
        },
        {
          text: self.analysis.personal.Name, // ESTJ
          style: 'subheader'
        },
        personalTrait,
        {
          text: 'General Description',
          style: 'subheader'
        },
        {
          text: self.analysis.personal.Description,
          style: 'content'
        },
        // College Styles
        {
          text: 'College Styles',
          style: 'header',
          alignment: 'center'
        },
        {
          text: self.analysis.college_career.CollegeStyles.Name,
          style: 'subheader'
        },
        collegeStyle,
        {
          text: 'General Description',
          style: 'subheader'
        },
        {
          text: self.analysis.college_career.CollegeStyles.Description,
          style: 'content'
        },
        {
          text: 'Searching for Colleges',
          style: 'sub2header'
        },
        {
          text: 'When looking for' + self.analysis.college_career.CollegeStyles.Name + 'here are some keywords to look for:' + keyWords ,
          style: 'content'
        },
        {
          text: self.analysis.college_career.CollegeStyles.Name +  'College Core Values and Approach',
          style: 'sub2header'
        },
        {
          ul: valueApproachArr
        },
        {
          text: 'Student Qualities that Match the' + self.analysis.college_career.CollegeStyles.Name + 'College Style',
          style: 'sub2header'
        },
        {
          ul: studentQualityArr
        },
        {
          text: 'Majors to Consider',
          style: 'header',
          alignment: 'center',
          backgroundColor: 'red'
        },
        {
          ul: majors
        },
        {
          text: 'Sample Careers',
          style: 'header',
          alignment: 'center'
        },
        {
          ul: careers
        },
      ]);
      self.ep.addStyle( {
        header: {
          fontSize: 25,
          bold: true,
          marginBottom: 20,
          marginTop: 20
        },
        subheader: {
          fontSize: 14,
          bold: true,
          color: '#006FD2',
          marginBottom: 15,
          marginTop: 15
        },
        sub2header: {
          fontSize: 13,
          marginBottom: 15,
          marginTop: 15,
          bold: true
        },
        content: {
          fontSize: 12,
          paddingLeft: 0
        }
      });
    });
  }

  getArr(mapArr, getArr) {
    if (mapArr.length > 0) {
      mapArr.forEach((item) => {
        const objTitle = { text: item.type.Name + ' - ' + item.type.Description , style: 'sub2header' };
        const objContent = { text: item.type.LongDescription, style: 'content' };
        getArr.push(objTitle);
        getArr.push(objContent);
      });
    } else {
      return;
    }
  }
  private getSurveyDetail() {
    const self = this;
    return new Promise(function (resolve, reject) {
      self.ss.getSurveyDetail(1).then(res => {
        self.details = res.sections;
        self.info = res;
        self.cd.markForCheck();
        resolve(res);
      });
    });
  }
  private getFmsAnalysis() {
    const self = this;
    return new Promise(function (resolve) {
      self.ss.getFmsAnalysis().then(res => {
        self.analysis = res;
        self.cd.markForCheck();
        resolve(res);
      });
    });
  }
  startSurvey() {
    this.router.navigateByUrl('/survey/question/' + this.info.first_section);
  }
  downloadPdf() {
    this.ep.openPdf();
    // pdfMake.createPdf(this.docDefinition).download();
  }
}
