import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-learningstyle-analysis',
  templateUrl: './learningstyle-analysis.component.html',
  styleUrls: ['./learningstyle-analysis.component.css']
})
export class LearningstyleAnalysisComponent implements OnInit {
  @Input('analysis') analysis: any = [];
  @Input('view') view: any[] = [1400, 300];

  // options
  @Input('showXAxis') showXAxis = true;
  @Input('showYAxis') showYAxis = true;
  @Input('gradient') gradient = false;
  @Input('showLegend') showLegend = false;
  @Input('showXAxisLabel ') showXAxisLabel = true;
  @Input('xAxisLabel') xAxisLabel = 'Score';
  @Input('showYAxisLabel') showYAxisLabel = false;
  @Input('yAxisLabel') yAxisLabel = 'Style';
  @Input('showDataLabel') showDataLabel = 'true';

  @Input('colorScheme') colorScheme = {
    domain: ['#A10A28', '#C7B42C', '#5AA454', '#AAAAAA']
  };

  constructor() { }

  ngOnInit() {
    console.log(this.analysis);
  }

}
