import { Component, OnInit, Input, DoCheck, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SurveyService } from '../../service/survey.service';
import { AuthService } from '../../service/auth.service';
import { EventEmitter, Output } from '@angular/core';
@Component({
  selector: 'app-description',
  templateUrl: './description.component.html',
  styleUrls: ['./description.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DescriptionComponent implements OnInit, DoCheck {
  @Input() info: any;
  @Output() exPdf = new EventEmitter();
  title = '';
  description = '';
  status = false;
  id = 0;
  test = null;
  constructor(private router: Router, private route: ActivatedRoute, private cd: ChangeDetectorRef,
    private ss: SurveyService, private auth: AuthService) { }

  ngOnInit() {

  }
  ngDoCheck() {
    this.updateInfo();
  }
  exportPdf() {
    this.exPdf.emit();
  }
  updateInfo() {
    // this.title = this.info.Name;
    // this.description = this.info.LongDescription;
    // this.status = this.info.status;
    // this.id = this.info.id;
    // this.cd.markForCheck();
  }
  startSurvey() {
    this.router.navigateByUrl('/survey/question/' + this.info.first_section);
  }
  updateSharing($event) {
    this.ss.updateSurveySharing(this.info.ID, $event.value).then(res => {
      // this.info.ID = res;
    });
  }
}
