import { Injectable } from '@angular/core';
import { FactoryService } from './factory.service';
import { AuthService } from './auth.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class SurveyService extends FactoryService {
  private layout: any;
  private auth:AuthService;
  constructor(protected http: HttpClient, private router:Router) {
    super(http);
  }

  public setAuth(auth:AuthService){
    this.auth=auth;
  }

  public setLayOut(layout: any) {
    this.layout = layout;
  }
  public hideHeaderAndBar() {
    this.layout.showHeaderAndBar(false);
  }
  public showHeaderAndBar() {
    this.layout.showHeaderAndBar(true);
  }

  public checkStudent(){
    if(this.auth.getStudentID()==0){
      this.router.navigateByUrl('/Home/AdvisorHome');
    }
  }
  public getSurveyQuestions(sectionID, studentID=0) {
    if(studentID==0){
      studentID=this.auth.getStudentID();
    }
    return new Promise<any>(resolve => {
      this.get(
        '/survey/common/getSection/?sectionID=' +
          sectionID +
          '&studentID=' +
          studentID
      ).then(res => {
        resolve(res);
      });
    });
  }

  //get detail for a certain survey
  public getSurveyDetail(surveyID, studentID=0) {
    if(studentID==0){
      studentID=this.auth.getStudentID();
    }
    return new Promise<any>(resolve => {
      this.get(
        '/survey/common/getSurveyDetail/?surveyID=' +
          surveyID +
          '&studentID=' +
          studentID
      ).then(res => {
        resolve(res);
      });
    });
  }

  //survey dashboard, use person ID
  public getSurveyDashboard(personID=0) {
    if(personID==0){
      personID=this.auth.getStudentID();
    }
    return new Promise<any>(resolve => {
      this.get(
        '/survey/common/getStudentSurveyDashboard/?studentID=' + personID
      ).then(res => {
        resolve(res);
      });
    });
  }

  //analysis result for colmatch
  public getColMatchAnalysis(personID=0) {
    if(personID==0){
      personID=this.auth.getStudentID();
    }
    return new Promise<any>(resolve => {
      this.get(
        '/survey/colmatch/getColMatchAnalysis/?studentID=' + personID
      ).then(res => {
        resolve(res);
      });
    });
  }

  //analysis result for learning styles
  public getLearningStyleAnalysis(personID=0) {
    if(personID==0){
      personID=this.auth.getStudentID();
    }
    return new Promise<any>(resolve => {
      this.get(
        '/survey/learningStyle/getLearningStyleAnalysis/?studentID=' + personID
      ).then(res => {
        resolve(res);
      });
    });
  }

  //analysis result for find my sparks
  public getFmsAnalysis(personID=0) {
    if(personID==0){
      personID=this.auth.getStudentID();
    }
    return new Promise<any>(resolve => {
      this.get(
        '/survey/fms/getFmsAnalysis/?studentID=' + personID
      ).then(res => {
        resolve(res);
      });
    });
  }

  //save after change
  public saveSurveySectionResponses(sectionID, responses, studentID=0) {
    if(studentID==0){
      studentID=this.auth.getStudentID();
    }
    const posts = {
      responses: responses
    };
    return new Promise<any>(resolve => {
      this.post(
        '/survey/common/saveSurveySectionResponses/?sectionID=' +
          sectionID +
          '&studentID=' +
          studentID,
        posts
      ).then(res => {
        resolve(res);
      });
    });
  }

  //update survey sharing status
  public updateSurveySharing(surveyID, sharingMode, studentID=0) {
    if(studentID==0){
      studentID=this.auth.getStudentID();
    }
    return new Promise<any>(resolve => {
      this.get(
        '/survey/common/updateSurveySharing/?studentID=' +
          studentID +
          '&surveyID=' +
          surveyID +
          '&sharingMode=' +
          sharingMode
      ).then(res => {
        resolve(res);
      });
    });
  }
}
