@Component({
  selector: 'app-survey-layout',
  templateUrl: '../customer.component.html',
  styleUrls: ['../customer.component.css'],
  providers: [SurveyService],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SurveyComponent implements OnInit, OnDestroy {
  show_header = true;
  show_sidebar = true;

  constructor(public router: Router, public auth: AuthService, private ss: SurveyService,
     private cd: ChangeDetectorRef) {
      this.ss.setLayOut(this);
      this.ss.setAuth(this.auth);
      this.ss.checkStudent();
  }

  ngOnInit() {
    this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd) {
      }
    });
  }
  ngOnDestroy(): void {
  }
  showHeaderAndBar(show: boolean) {
    this.show_header = show;
    this.show_sidebar = show;
  }
}
