import { Component, OnInit, ChangeDetectionStrategy, DoCheck, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SurveyService } from '../../service/survey.service';
@Component({
  selector: 'app-question',
  templateUrl: './question.component.html',
  styleUrls: ['./question.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class QuestionComponent implements OnInit, DoCheck, OnDestroy {
  // title = 'part 1';
  // survey_name = 'Find My Spark';
  section: any = [];
  questions: any = [{
    index: 1,
    name: 'question 1',
    val: 0,
    req: true,
    type: 1
  },
  {
    idnex: 2,
    name: 'question 2',
    val: 'haha',
    req: true,
    type: 2
  }
  ];
  saving = false;
  constructor(private router: Router, private route: ActivatedRoute, private ss: SurveyService, private cd: ChangeDetectorRef) {
  }

  ngOnInit() {
    this.ss.hideHeaderAndBar();
    this.route.params.subscribe(data => {
      this.ss.getSurveyQuestions(data.id).then(result => {
        this.questions = result.questions;
        this.section = result;
        this.cd.markForCheck();
      });
    });
  }

  save(next = 0, complete = false) {
    if (this.saving) {return; }
    this.saving = true;
    const questions = [];
    for (let i = 0; i < this.questions.length; i++) {
      questions.push(this.questions[i].response);
    }
    this.ss.saveSurveySectionResponses(this.section.ID, questions).then(res => {
      if (next === 1 && this.section.nextSectionID !== undefined) {
          this.router.navigateByUrl('/survey/question/' + this.section.nextSectionID);
      } else if (next === -1 && this.section.prevSectionID !== undefined) {
        this.router.navigateByUrl('/survey/question/' + this.section.prevSectionID);
      } else if (complete) {
        this.router.navigateByUrl('/survey/dashboard');
      } else if (res !== 0) {
        this.questions = res['questions'];
        this.section = res;
        this.cd.markForCheck();
      }
      this.saving = false;
    });
  }
  ngDoCheck() {

  }
  ngOnDestroy() {
    this.ss.showHeaderAndBar();
  }
  checkQuestions() {
    console.log(this.questions);
  }
  cancel() {
    this.router.navigateByUrl(this.section.detail_url);
  }
}
