import {
  Component,
  OnInit,
  Input,
  DoCheck,
  ChangeDetectionStrategy,
  ChangeDetectorRef
} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SurveyService } from '../../service/survey.service';

/**
 * @title Basic grid-list
 */
// @Component({
//   selector: 'app-survey-dashboard',  
//   templateUrl: './dashboard.component.html',
//   styleUrls: ['./dashboard.component.css'],
// })
// export class GridListOverviewExample {}

@Component({
  selector: 'app-survey-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DashboardComponent implements OnInit {
  multi = [
    {
      name: 'Surveys',
      series: [
        {
          name: 'Not Started',
          value: 3
        },
        {
          name: 'In-Progress',
          value: 4
        },
        {
          name: 'Finished',
          value: 8
        }
      ]
    }
  ];
  surveys = [
    {
      name: 'Learn',
      color: '#5AA454',
      survey: [
        {
          name: 'Find My Spark',
          status: 'In Progress',
          id: 1,
          questions: {
            finished: 1,
            total: 2
          }
        },
        {
          name: 'College Match',
          status: 'Completed',
          id: 2,
          questions: {
            finished: 2,
            total: 2
          }
        }
      ]
    },
    {
      name: 'Explore',
      color: '#C7B42C',
      survey: [
        {
          name: 'Explore #1',
          status: 'In Progress',
          id: 3,
          questions: {
            finished: 1,
            total: 2
          }
        },
        {
          name: 'Explore #2',
          status: 'Completed',
          id: 4,
          questions: {
            finished: 2,
            total: 2
          }
        }
      ]
    }
  ];
  colors = { Explore: '#C7B42C', Learn: '#5AA454', Record: '#123332' };
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private cd: ChangeDetectorRef,
    private ss: SurveyService
  ) {
    // Object.assign(this, { single, multi });
  }

  ngOnInit() {
    this.ss.getSurveyDashboard().then(res => {
      const keylist = Object.keys(res);
      this.surveys = [];
      const freq = [0, 0, 0];
      for (const key of keylist) {
        this.surveys.push({
          name: key,
          color: this.colors[key],
          survey: res[key]
        });
        for (const survey of res[key]) {
          if (survey.ratio === 0) {
            freq[0]++;
          }
          if (survey.ratio > 0 && survey.ratio < 1) {
            freq[1]++;
          }
          if (survey.ratio === 1) {
            freq[2]++;
          }
        }
      }
      for (let i = 0; i < 3; i++) {
        this.multi[0]['series'][i]['value'] = freq[i];
      }
      this.multi = JSON.parse(JSON.stringify(this.multi));
      this.cd.markForCheck();
    });
  }
}
