import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef
} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SurveyService } from '../../service/survey.service';

@Component({
  selector: 'app-common',
  templateUrl: './common.component.html',
  styleUrls: ['./common.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CommonComponent implements OnInit {
  baseLink = '/survey/common/';
  links = [
    { link: '/summary', label: 'Summary' },
    { link: '/progress', label: 'Survey Progress' }
  ];
  id = 0;
  info: any = {
    name: 'Survey',
    description: 'A survey',
    status: false,
    id: 0
  };
  details: any = [];
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private ss: SurveyService,
    private cd: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.route.params.subscribe(data => {
      this.id = data['id'];
      this.getSurveyDetail();
    });
  }
  private getSurveyDetail() {
    this.ss.getSurveyDetail(this.id).then(res => {
      this.details = res.sections;
      this.info = res;
      this.cd.markForCheck();
    });
  }
  startSurvey() {
    this.router.navigateByUrl('/survey/question/' + this.info.first_section);
  }
}
